  }
  }

  textarea.form-control.type_msg {
    resize: none;
    padding: 18px 0;
}
.input-group-append button[type=submit]{
  background: transparent;
  border: none;
  outline: none;
  padding: 0;
}
span.input-group-text.send_btn {
  padding: 1.375rem .75rem;
}

.list__user{
  cursor: pointer;
}

.box__search{
  position: absolute;
  width: calc(100% + 1.25rem * 2);
  top: calc(100% + 0.75rem);
  left: -1.25rem;
  height: 440px;
  z-index: 9;
  background: #242526;
  padding: 0.5rem 1.25rem;
}
.title__search{
  color: #b0b3b8;
  font-size: 15px;
  font-weight: bold;
}
.warpper__search{
  color: #e4e6eb;
  margin-top: 0.5rem;
}
.list__search{
  display: flex;
  cursor: pointer;
  padding: 5px 0;
}
.list__search:hover{
  background-color: #292a2c;
}
.search__avatar{
  width: 45px;
}

.search__avatar img{
  width: 45px;
  height: 45px;
  border-radius: 100%;
}
.search__info{
  margin-left: 20px;
  padding-top: 8px;
}
.info__name{
  font-size: 15px;
  display: block;
  line-height: 10px;
}
.info__status{
  font-size: 10px;
  line-height: 5px;
  position: relative;
  padding-left: 20px;
}
span.info__status::before {
    content: '';
    position: absolute;
    display: inline-block;
    width: 10px;
    border-radius: 100%;
    height: 10px;
    top: 2px;
    left: 5px;
}

span.info__status.offline::before{
  background: #ccc;
}
span.info__status.online::before{
  background : #31a24c;
}
span.info__status.offline{
  background-color: transparent !important;
}
